.navbar{
  border-radius: 4px;
}
.navbar-dark .navbar-brand{
  font-size: 20px;
  text-transform: uppercase;
}
.nav-item{
  text-transform: uppercase;
  margin-left: 50px;
}
.nav-item a::after{
  content: '';
  display: block;
  width: 100%;
  background-color: #ccc;
  height: 1px;
  transform: scale(0);
  transition: all .3s;
}
.nav-item a:hover::after{
  transform: scale(1);
}
.nav-item .active{
  text-shadow: 1px 0px 5px #fff;
}
@media(max-width: 1024px) {
  .navbar{
    padding: 20px;
  }
  .navbar-dark .navbar-brand, .navbar-nav{
    font-size: 17px;
  }
}
@media(max-width: 768px) {
  .navbar-dark .navbar-brand{
    font-size: 15px;
  }
  .navbar-nav{
    justify-content: center;
    align-items: center;
    font-size: 15px;
  }
}
@media(max-width: 414px){
  .nav-item a::after{
    content: '';
    height: 0;
  }
  .navbar-nav{
    margin-top: 5px;
    justify-content: center;
    align-items: flex-end;
    font-size: 13px;
    line-height: 3px;
  }
}